<template>
  <div>
    <a-form-model :model="form" :label-col="labelCol" :wrapper-col="wrapperCol">
      <a-form-model-item label="姓名">
        <a-input v-model="form.name" />
      </a-form-model-item>
      <a-form-model-item label="专业">
        <a-select v-model="form.major" placeholder="请选择你的专业">
          <a-select-option value="cs"> 计算机科学与技术 </a-select-option>
          <a-select-option value="ee"> 软件工程 </a-select-option>
          <a-select-option value="db"> 数据库 </a-select-option>
          <a-select-option value="electric"> 电子信息技术 </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="出生日期">
        <a-date-picker
          v-model="form.birthday"
          :locale="locale"
          type="date"
          placeholder="请选择日期"
          style="width: 100%"
        />
      </a-form-model-item>
      <a-form-model-item label="是否毕业">
        <a-switch v-model="form.isGraduate" />
      </a-form-model-item>
      <a-form-model-item label="爱好">
        <a-checkbox-group v-model="form.hobby">
          <a-checkbox value="1" name="type"> 听歌 </a-checkbox>
          <a-checkbox value="2" name="type"> 看电影 </a-checkbox>
          <a-checkbox value="3" name="type"> 打篮球 </a-checkbox>
        </a-checkbox-group>
      </a-form-model-item>
      <a-form-model-item label="性别">
        <a-radio-group v-model="form.gender">
          <a-radio value="1"> 男 </a-radio>
          <a-radio value="2"> 女 </a-radio>
        </a-radio-group>
      </a-form-model-item>
      <a-form-model-item label="备注">
        <a-input v-model="form.desc" type="textarea" />
      </a-form-model-item>
      <a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
        <a-button type="primary" @click="onSubmit"> 确认 </a-button>
        <a-button style="margin-left: 10px" @click='cancel'> 取消 </a-button>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>
<script>
import locale from "ant-design-vue/es/date-picker/locale/zh_CN";
import { mapState, mapMutations, mapActions } from "vuex";
import Moment from "moment";

export default {
  data() {
    return {
      name: "studentNew",
      labelCol: { span: 4 },
      wrapperCol: { span: 14 },
      form: {
        name: "",
        major: undefined,
        birthday: undefined,
        isGraduate: false,
        hobby: [],
        gender: "",
        desc: "",
      },
      locale,
    };
  },
  computed: {
    ...mapState(["students"]),
  },
  methods: {
    ...mapActions(["addStudent"]),
    onSubmit() {
      this.form.birthday = this.$moment(this.form.birthday).format(
        "YYYY-MM-DD"
      );
      console.log("submit!", this.form);
      this.addStudent(this.form);
      this.$router.push("student");
    },
    cancel(){
        this.$router.push("student");
    }
  },
};
</script>
