import Vue from 'vue'
import App from './App.vue'

import Vuex from 'vuex'
import router from './router'

import Antd from 'ant-design-vue';
import 'ant-design-vue/dist/antd.css';

import moment from 'moment'

Vue.prototype.$moment = moment
import 'moment/locale/zh-cn';
//定义一个全局过滤器实现日期格式化
Vue.filter('datefmt',function (input,fmtstring) {//当input为时间戳，需转为Number类型
  if(input){
     return moment(input).format(fmtstring);
 }else{
     return "";
 }
});

Vue.use(Antd);
Vue.use(Vuex)

Vue.config.productionTip = false


import {
  menus
} from './basedate/menu'

import {
  student
} from './basedate/studentData.js'

const store = new Vuex.Store({
  state: {
    start: 0,
    students: student,
    menus:menus
  },
  mutations: {
    increment(state) {
      state.count++
    },
    setStudents(state, stus) {
      state.students = stus
    }
  },
  actions: {
    addStudent(context, stu) {
      let arr = context.state.students
      arr.push(stu)
      context.commit('setStudents', arr)
    },
    removeStudent(context, stu){
      let arr = context.state.students
      let index = stu.key
      arr.splice(index,1)
      context.commit('setStudents', arr)
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')