<template>
  <div id="demo-wrapper">
    <div id="content-demo"></div>
  </div>
</template>
<script>
import * as echarts from "echarts";
// 基于准备好的dom，初始化echarts实例
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  name: "demo",
  data() {
    return {};
  },
  computed: {
    ...mapState(["students"]),
    chartsData() {
      let cs = 0;
      let ee = 0;
      let electric = 0;
      let db = 0;
      for (let stu of this.students) {
        // console.log("stu", stu);
        if (stu.major == "cs") {
          cs++;
        } else if (stu.major == "ee") {
          ee++;
        } else if (stu.major == "db") {
          db++;
        } else if (stu.major == "electric") {
          electric++;
        }
      }
      return [cs, db, electric, ee];
    },
  },

  mounted() {
    window.setTimeout(() => {
      this.initCharts();
    }, 1);
  },
  methods: {
    initCharts() {
      var myChart = echarts.init(document.getElementById("content-demo"));
      // 绘制图表
      myChart.setOption({
        title: {
          text: "计算机系专业学生人数",
        },
        tooltip: {},
        xAxis: {
          data: ["计算机科学与技术", "数据库", "电子技术", "软件工程"],
        },
        yAxis: {},
        series: [
          {
            name: "销量",
            type: "bar",
            data: this.chartsData,
          },
        ],
      });
    },
  },
};
</script>
<style scoped>
#demo-wrapper {
  height: 100%;
  width: 100%;
}
#content-demo {
  height: 100%;
  width: 100%;
}
</style>