import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '../components/login/login.vue'
import index from '../components/index/index.vue'
import demo from '../components/content/charts/demo.vue'
import earth from '../components/content/charts/earth.vue'
import roulous from '../components/content/charts/roulous.vue'
import pie from '../components/content/charts/pie.vue'

import student from '../components/content/data/student.vue'
import studentNew from '../components/content/data/studentNew.vue'


Vue.use(VueRouter)

const routes = [{
    path: '/',
    redirect: '/index'
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/index',
    name: 'index',
    component: index,
    children: [{
        path: 'demo',
        component: demo
      },
      {
        path: 'earth',
        component: earth
      },
      {
        path: 'roulous',
        component: roulous
      },
      {
        path: 'pie',
        component: pie
      },
      {
        path: 'student',
        component: student,
      },
      {
        path: 'studentNew',
        component: studentNew
      }
    ]
  },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  // mode: 'history',
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  let username = sessionStorage.getItem("username")
  console.log("username", username)
  if (to.name !== 'login' && !username) next({
    name: 'login'
  })
  else next()
})

export default router