<template>
  <div id="demo-wrapper">
    <div id="content-earth"></div>
  </div>
</template>
<script>
import * as echarts from "echarts";
import "echarts-gl";
// 基于准备好的dom，初始化echarts实例

export default {
  name: "demo",
  data() {
    return {};
  },
  mounted() {
    window.setTimeout(()=>{
      this.initCharts()
    },1)
  },
  methods: {
    initCharts() {
      var ROOT_PATH =
        "https://cdn.jsdelivr.net/gh/apache/echarts-website@asf-site/examples";

      var chartDom = document.getElementById("content-earth");
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        backgroundColor: "#000",
        globe: {
          baseTexture: ROOT_PATH + "/data-gl/asset/earth.jpg",
          heightTexture:
            ROOT_PATH + "/data-gl/asset/bathymetry_bw_composite_4k.jpg",
          displacementScale: 0.1,
          shading: "lambert",
          environment: ROOT_PATH + "/data-gl/asset/starfield.jpg",
          light: {
            ambient: {
              intensity: 0.1,
            },
            main: {
              intensity: 1.5,
            },
          },
          layers: [
            {
              type: "blend",
              blendTo: "emission",
              texture: ROOT_PATH + "/data-gl/asset/night.jpg",
            },
            {
              type: "overlay",
              texture: ROOT_PATH + "/data-gl/asset/clouds.png",
              shading: "lambert",
              distance: 5,
            },
          ],
        },
        series: [],
      };

      option && myChart.setOption(option);
    },
  },
};
</script>
<style scoped>
#demo-wrapper {
  height: 100%;
  width: 100%;
}
#content-earth {
  height: 100%;
  width: 100%;
}
</style>