<template>
  <div class="student-wrapper">
    <div class="view" v-show="viewType == 1">
      <div class="btn-area">
        <router-link to="studentNew"
          ><a-button type="primary">新建</a-button></router-link
        >
      </div>
      <a-table
        :columns="columns"
        :data-source="formatedStudents"
        :scroll="{ x: 1300 }"
        :pagination="{ defaultPageSize: 6 }"
      >
        <a
          slot="action"
          slot-scope="record"
          href="javascript:;"
          @click="deleteRow(record)"
          >删除</a
        >
      </a-table>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from "vuex";

const columns = [
  {
    title: "姓名",
    width: 100,
    dataIndex: "name",
    key: "name",
    fixed: "left",
  },
  {
    title: "专业",
    width: 200,
    dataIndex: "majorStr",
    key: "majorStr",
    fixed: "left",
  },
  { title: "性别", dataIndex: "genderStr", key: "genderStr" },
  { title: "生日", dataIndex: "birthday", key: "birthday" },
  { title: "是否毕业", dataIndex: "isGraduate", key: "isGraduate" },
  { title: "爱好", dataIndex: "hobbyStr", key: "hobbyStr" },
  { title: "备注", dataIndex: "desc", key: "desc" },
  {
    title: "操作",
    key: "operation",
    fixed: "right",
    width: 100,
    scopedSlots: { customRender: "action" },
  },
];

export default {
  data() {
    return {
      name: "student",
      // data,
      columns,
      viewType: 1,
    };
  },
  computed: {
    ...mapState(["students"]),
    formatedStudents() {
      let arr = [];
      for (let i = 0; i < this.students.length; i++) {
        let stu = this.students[i];
        let majorStr = ''
        if (stu.major == "cs") {
          majorStr = "计算机科学与技术";
        } else if (stu.major == "ee") {
          majorStr = "软件工程";
        } else if (stu.major == "db") {
          majorStr = "数据库";
        } else if (stu.major == "electric") {
          majorStr = "电子技术";
        }
        if (stu.isGraduate) {
          stu.isGraduate = "是";
        } else {
          stu.isGraduate = "否";
        }
        let s = "";
        for (let j in stu.hobby) {
          let ho = stu.hobby[j];
          if (ho == "1") {
            s += "听歌,";
          } else if (ho == "2") {
            s += "看电影,";
          } else if (ho == "3") {
            s += "打篮球,";
          }
        }
        if (s.length > 1) {
          s = s.substring(0, s.length - 1);
        }
        let genderStr = "男";
        if (stu.gender == "2") {
          genderStr = "女";
        }
        stu.majorStr = majorStr
        stu.hobbyStr = s;
        stu.key = i;
         stu.genderStr = genderStr;
        arr.push(stu);
      }
      return arr;
    },
  },
  methods: {
    ...mapActions(["addStudent", "removeStudent"]),
    deleteRow(record) {
      this.$confirm({
        title: "确定要删除[" + record.name + "]吗?",
        content: "此操作不可逆!!",
        okText: "确认",
        okType: "danger",
        cancelText: "取消",
        onOk: () => {
          this.removeStudent(record);
          console.log("OK");
        },
        onCancel: () => {
          console.log("Cancel");
        },
      });
    },
  },
};
</script>
<style scoped>
.pieData-wrapper {
  height: 100%;
  width: 100%;
}
</style>