<template>
  <div id="demo-wrapper">
    <div id="content-pie"></div>
  </div>
</template>
<script>
import * as echarts from "echarts";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState(["students"]),
    pieData() {
      let girlsNum = 0;
      let boysNum = 0;
      for (let stu of this.students) {
        // console.log('stu',stu)
        if (stu.gender == "1") {
          boysNum++;
        } else {
          girlsNum++;
        }
      }
      return [
        { name: "男生", value: boysNum },
        { name: "女生", value: girlsNum },
      ];
    },
  },
  mounted() {
    window.setTimeout(()=>{
      this.initCharts()
    },1)
  },
  methods: {
    initCharts() {
      var chartDom = document.getElementById("content-pie");
      var myChart = echarts.init(chartDom);
      var option;

      option = {
        title: {
          text: "计算机系男女比例",
          subtext: "",
          left: "center",
        },
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          left: "left",
        },
        series: [
          {
            name: "Access From",
            type: "pie",
            radius: "50%",
            data: this.pieData,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      };
      option && myChart.setOption(option);
    },
  },
};
</script>
<style scoped>
#demo-wrapper {
  height: 100%;
  width: 100%;
}
#content-pie {
  height: 100%;
  width: 100%;
}
</style>