<template>
  <a-layout id="index-wrapper">
    <a-layout-header class="header">
      <div class="logo">图表展示</div>
      <!-- <a-menu
        theme="dark"
        mode="horizontal"
        :default-selected-keys="['2']"
        :style="{ lineHeight: '64px' }"
      > -->
      <!-- <a-menu-item key="1"> nav 1 </a-menu-item>
        <a-menu-item key="2"> nav 2 </a-menu-item>
        <a-menu-item key="3"> nav 3 </a-menu-item> -->

      <!-- </a-menu> -->
      <div class="user-info">
        <a-avatar :size="32" icon="user" />
        <a-dropdown :trigger="['click']">
          <a class="ant-dropdown-link" @click="(e) => e.preventDefault()">
            {{ username }}<a-icon type="down" />
          </a>
          <a-menu slot="overlay">
            <a-menu-item key="0">
              <a href="http://www.alipay.com/">个人中心</a>
            </a-menu-item>
            <a-menu-divider />
            <a-menu-item key="2" @click="logout"> 退出登录 </a-menu-item>
          </a-menu>
        </a-dropdown>
      </div>
    </a-layout-header>
    <a-layout>
      <a-layout-sider width="200" style="background: #fff">
        <a-menu
          mode="inline"
          :selected-keys="selectedKeys"
          :open-keys="openKeys"
          @openChange="openChange"
          :style="{ height: '100%', borderRight: 0 }"
        >
          <template v-for="first in menus">
            <a-menu-item
              v-if="!first.children"
              @click="jump(first.path)"
              :key="first.key"
            >
              <span><a-icon :type="first.icon" /> </span>
              {{ first.name }}
            </a-menu-item>
            <a-sub-menu v-else :key="first.key">
              <span slot="title"
                ><a-icon :type="first.icon" />{{ first.name }}</span
              >
              <a-menu-item
                v-for="second in first.children"
                :key="second.key"
                @click="jump(second.path)"
              >
                {{ second.name }}
              </a-menu-item>
            </a-sub-menu>
          </template>
        </a-menu>
      </a-layout-sider>
      <a-layout style="padding: 0 24px 24px">
        <a-breadcrumb style="margin: 16px 0">
          <a-breadcrumb-item v-for ="item in breadcrumb" :key="item.key">{{item.name}}</a-breadcrumb-item>
          <!-- <a-breadcrumb-item>App</a-breadcrumb-item> -->
        </a-breadcrumb>
        <a-layout-content
          :style="{
            background: '#fff',
            padding: '24px',
            margin: 0,
            minHeight: '280px',
          }"
        >
          <transition name="slide-fade">
            <router-view></router-view>
          </transition>
        </a-layout-content>
      </a-layout>
    </a-layout>
  </a-layout>
</template>
<script>
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  name: "index",
  data() {
    return {
      collapsed: false,
      username: "",
      openKeys: [],
    };
  },
  computed: {
    ...mapState(["menus"]),
    selectedKeys() {
      return [this.$route.path.substr(this.$route.path.lastIndexOf("/") + 1)];
    },
    breadcrumb(){
      let selectKey = this.selectedKeys[0]
      let openKeys = this.calcOpenKeys(selectKey)
      let openKey = openKeys[0]
      let openName = ''
      let selectName = ''
      for (let first of this.menus) {
        if(openKey ==  first.key){
          openName = first.name
        }
        let children = first.children;
        // console.log("children", children);
        if (children) {
          for (let second of children) {
            if (second.key == selectKey) {
              selectName = second.name
            }
          }
        }
      }
      return [
        {key:openKey,name:openName},
        {key:selectKey,name:selectName}
      ] 
    }
  },
  mounted() {
    let username = sessionStorage.getItem("username");
    this.username = username;
    this.openKeys = this.calcOpenKeys("");
  },
  methods: {
    jump(link) {
      // this.calcOpenKeys(link);
      this.$router.push("/index/" + link);
    },
    logout() {
      sessionStorage.setItem("username", "");
      this.$router.push("login");
    },
    calcOpenKeys(path) {
      if (path == "") {
        path = this.$route.path.substr(this.$route.path.lastIndexOf("/") + 1);
      }
      // console.log("path", path);
      for (let first of this.menus) {
        let children = first.children;
        // console.log("children", children);
        if (children) {
          for (let second of children) {
            if (second.key == path) {
              // console.log("key", first.key);
              let arr = []
              arr.push(first.key)
              return arr
            }
          }
        }
      }
      return [] 
    },
    // select({ item, key, selectedKeys }) {
    //   this.selectedKeys = [key];
    // },
    openChange(keys) {
      this.openKeys = keys;
    },
  },
};
</script>

